/* Navbar styles */
.navbar {
  background-color: rgba(255, 243, 216, 0.91);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5vh;
  height: 12vh;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: sticky;
  font-family: "Fugaz One", sans-serif;
  font-weight: 400;
  font-style: normal;
}

/* Logo styles */
.logo {
  height: 10vh;
  width: auto;
  cursor: pointer;
  object-fit: contain;
}

.list-div {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

/* List styles */
.list-div ul {
  display: flex;
  gap: 40px;
  margin: 0;
  padding: 0;
}

.list-div ul li {
  list-style: none;
  font-size: 18px;
  cursor: pointer;
}

/* Search box styles */
.search-box {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(54, 51, 44);
  padding: 8px 16px;
  border-radius: 50px;
  height: 50px; /* fixed height for consistency */
  box-sizing: border-box;
}

/* Input styles */
.search-box input {
  background: transparent;
  border: none;
  outline: none;
  color: #fff;
  font-size: 16px;
  margin-right: 8px;
  line-height: 1;
  max-width: 200px;
}

.search-box input::placeholder {
  color: #fff;
}

/* Search icon */
.search-box img {
  width: 20px;
  height: 20px;
  display: block;
}
